<template>
  <div class="love">
    <div class="text">
      ÂìàÂìàÂìàÂìàÂìàÔºåÊâì‰∏çËøáÊàëÂêß-----Ê≤°ÊúâÂäûÊ≥ïÔºåÊàëÂ∞±ÊòØËøô‰πàÂº∫Â§ßÔºÅ
    </div>



  </div>
</template>

<script>
import '../assets/js/m.js'
//import $ from 'n-zepto'

export default {
  name: 'Love',
  data () {
    return {
      type: 0,
      contentArr: [
        'ÊàøËØÅÂÜô‰Ω†ÂêçüòÅ', '‰øùÂ§ßüòÅ', 'ÊàëÂ¶à‰ºöÊ∏∏Ê≥≥üòÅ', 'Â∑•ËµÑÂÖ®Áªô‰Ω†üòÅ', 'Â§©Â§©ÁªôÊåâÊë©üòÅ', 'ÂêµÊû∂ÂÖàÈÅìÊ≠âüòÅ', 'ÁªèÂ∏∏ÈÄÅÁ§ºÁâ©üòÅ', 'Â§©Â§©ÊÉÖ‰∫∫ËäÇüòÅ',
        'ËÆ©‰Ω†Ê¨∫Ë¥ü‰∏ÄËæàÂ≠êüòÅ', 'Â§©Â§©ÈÄó‰Ω†ÂºÄÂøÉüòÅ'
      ]
    }
  },
  created () {
    this.$messagebox.confirm('', {
      title: 'ÂÅöÊàëÂ•≥ÊúãÂèã',
      message: 'Â•ΩÂêóÔºüÂ•ΩÂêóÔºüÂ•ΩÂêóÔºü',
      showConfirmButton: true,
      confirmButtonText: 'Â•Ω',
      showCancelButton: true,
      cancelButtonText: '‰∏çË¶Å'
    }).then(action => {
      if (action == 'confirm') {     //Á°ÆËÆ§ÁöÑÂõûË∞É
        this.success()

      }

    }).catch(err => {
      if (err == 'cancel') {     //ÂèñÊ∂àÁöÑÂõûË∞É
        this.callback()

      }
    })


  },
  mounted () {
    console.log(wx)
    wx.config({
      debug: false,
      appId: 'wx78db3ae4f16b702c', // ÂøÖÂ°´ÔºåÂÖ¨‰ºóÂè∑ÁöÑÂîØ‰∏ÄÊ†áËØÜ
      timestamp: 1535369454311, // ÂøÖÂ°´ÔºåÁîüÊàêÁ≠æÂêçÁöÑÊó∂Èó¥Êà≥
      nonceStr: 'abc', // ÂøÖÂ°´ÔºåÁîüÊàêÁ≠æÂêçÁöÑÈöèÊú∫‰∏≤
      signature:'abc',// ÂøÖÂ°´ÔºåÁ≠æÂêçÔºåËßÅÈôÑÂΩï1
      jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage','error'] // ÂøÖÂ°´ÔºåÈúÄË¶Å‰ΩøÁî®ÁöÑJSÊé•Âè£ÂàóË°®ÔºåÊâÄÊúâJSÊé•Âè£ÂàóË°®ËßÅÈôÑÂΩï2
    });
    wx.ready(function () {
      //Ëé∑Âèñ‚ÄúÂàÜ‰∫´Âà∞ÊúãÂèãÂúà‚ÄùÊåâÈíÆÁÇπÂáªÁä∂ÊÄÅÂèäËá™ÂÆö‰πâÂàÜ‰∫´ÂÜÖÂÆπÊé•Âè£
      wx.onMenuShareTimeline({
        title: '', // ÂàÜ‰∫´Ê†áÈ¢ò
        link: '', // ÂàÜ‰∫´ÈìæÊé•ÔºåËØ•ÈìæÊé•ÂüüÂêçÊàñË∑ØÂæÑÂøÖÈ°ª‰∏éÂΩìÂâçÈ°µÈù¢ÂØπÂ∫îÁöÑÂÖ¨‰ºóÂè∑JSÂÆâÂÖ®ÂüüÂêç‰∏ÄËá¥
        imgUrl: '', // ÂàÜ‰∫´ÂõæÊ†á
        success: function () {
          alert('Á°ÆËÆ§ÂàÜ‰∫´')
          // Áî®Êà∑Á°ÆËÆ§ÂàÜ‰∫´ÂêéÊâßË°åÁöÑÂõûË∞ÉÂáΩÊï∞
        },
        cancel: function () {
          // Áî®Êà∑ÂèñÊ∂àÂàÜ‰∫´ÂêéÊâßË°åÁöÑÂõûË∞ÉÂáΩÊï∞
        }
      });
      //Ëé∑Âèñ‚ÄúÂàÜ‰∫´ÁªôÊúãÂèã‚ÄùÊåâÈíÆÁÇπÂáªÁä∂ÊÄÅÂèäËá™ÂÆö‰πâÂàÜ‰∫´ÂÜÖÂÆπÊé•Âè£
      wx.onMenuShareAppMessage({
        title: '', // ÂàÜ‰∫´Ê†áÈ¢ò
        desc: '', // ÂàÜ‰∫´ÊèèËø∞
        link: '', // ÂàÜ‰∫´ÈìæÊé•ÔºåËØ•ÈìæÊé•ÂüüÂêçÊàñË∑ØÂæÑÂøÖÈ°ª‰∏éÂΩìÂâçÈ°µÈù¢ÂØπÂ∫îÁöÑÂÖ¨‰ºóÂè∑JSÂÆâÂÖ®ÂüüÂêç‰∏ÄËá¥
        imgUrl: '', // ÂàÜ‰∫´ÂõæÊ†á
        type: '', // ÂàÜ‰∫´Á±ªÂûã,music„ÄÅvideoÊàñlinkÔºå‰∏çÂ°´ÈªòËÆ§‰∏∫link
        dataUrl: '', // Â¶ÇÊûútypeÊòØmusicÊàñvideoÔºåÂàôË¶ÅÊèê‰æõÊï∞ÊçÆÈìæÊé•ÔºåÈªòËÆ§‰∏∫Á©∫
        success: function () {
          // Áî®Êà∑Á°ÆËÆ§ÂàÜ‰∫´ÂêéÊâßË°åÁöÑÂõûË∞ÉÂáΩÊï∞
        },
        cancel: function () {
          // Áî®Êà∑ÂèñÊ∂àÂàÜ‰∫´ÂêéÊâßË°åÁöÑÂõûË∞ÉÂáΩÊï∞
        }
      });
    })


  },
  methods: {
    callback () {
      if (this.type >= this.contentArr.length) {
        this.type = 0
      }

      const content = this.contentArr[this.type]
      this.type++

      this.dialog(null, content, this.success, this.callback)
    },
    success () {
      this.$messagebox.alert('‰ªé‰ªäÂ§©Ëµ∑‰Ω†‰∏çÊòØÂä®Áâ©‰∫Ü', '‰Ω†Â§™ÊúâÁúºÂÖâ‰∫ÜÔºÅ')

    },

    dialog (title = '', content = '', success, fail) {
      this.$messagebox.confirm('', {
        title: title,
        message: content,
        showConfirmButton: true,
        confirmButtonText: 'Â•Ω',
        showCancelButton: true,
        cancelButtonText: '‰∏çË¶Å'
      }).then(action => {
        if (action == 'confirm') {     //Á°ÆËÆ§ÁöÑÂõûË∞É
          success && success()
        }

      }).catch(err => {
        if (err == 'cancel') {     //ÂèñÊ∂àÁöÑÂõûË∞É
          fail && fail()
        }
      })

    }

  }

}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  @import '../assets/css/pr2rem';

  .text {
    width: pr(600);
    height: pr(200);
    font-size: pr(30);
    position: fixed;
    top: 50%;
    left: 50%;
    margin-top: pr(-100);
    margin-left: pr(-300);
  }

</style>



